/******* CLASSES GENERALES  *******/
.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

.hidden {
  display: none;
}

.align-center {
  text-align: center;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.no-transition {
  transition: none !important;
}

.no-list {
  list-style: none;
}

.icon {
  fill: currentColor;
  stroke: currentColor;
}

.secondary {
  color: #3aafa9;
}

.title {
  margin: 0 0 5px;
  text-transform: uppercase;
  font-weight: 900;
}

.wp-block-buttons {
  margin: 35px 0 85px;
}

.wp-block-pullquote {
  padding: 0;
}
.wp-block-pullquote p {
  font-size: inherit;
  line-height: inherit;
}
.wp-block-pullquote cite {
  display: block;
  margin: 30px 0 0;
  font-size: 1.2rem;
  font-style: normal;
}
.wp-block-pullquote + .wp-block-media-text {
  margin-top: 0;
}

.video {
  height: 0;
  padding: 0 0 56%;
  position: relative;
}
.video > .cover,
.video > .cover-small,
.video > iframe {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.cover,
.cover-small {
  z-index: 10;
  background-size: cover;
  background-position: 50% 50%;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.cover.off,
.cover-small.off {
  opacity: 0;
  pointer-events: none;
}
.cover:hover:before, .cover:focus:before,
.cover-small:hover:before,
.cover-small:focus:before {
  transform: scale(1.05);
}
.cover:hover:after, .cover:focus:after,
.cover-small:hover:after,
.cover-small:focus:after {
  transform: scale(0.98);
  opacity: 0.9;
}
.cover:before, .cover:after,
.cover-small:before,
.cover-small:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border-radius: 50%;
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
}
.cover:before,
.cover-small:before {
  width: 60px;
  height: 60px;
  background: rgba(255, 255, 255, 0.5);
  opacity: 0.7;
  backdrop-filter: blur(24px);
}
.cover:after,
.cover-small:after {
  width: 45px;
  height: 45px;
  background: #fff url(../../img/play.svg) no-repeat 55% 50%;
  background-size: 15px;
  filter: drop-shadow(0 0 6px rgba(0, 0, 0, 0.3));
  will-change: transform;
}

