/* Placeholders */
html.no-scroll {
  position: fixed;
  left: 0;
  right: 0;
}

/* Buttons */
.btn,
.btn-invert,
.wp-block-button__link,
.hs-button {
  display: inline-block;
  padding: 0.5em 2.5em;
  font-family: "Roboto Slab", "Century Gothic", sans-serif;
  font-weight: 900;
  font-size: 0.95em;
  border-radius: 4px;
}

.btn,
.wp-block-button__link,
.hs-button {
  border: 2px solid #3aafa9;
  background: #3aafa9;
  color: #fff;
}
.btn:hover, .btn:focus,
.wp-block-button__link:hover,
.wp-block-button__link:focus,
.hs-button:hover,
.hs-button:focus {
  background: #fff;
  color: #3aafa9;
  border-color: #3aafa9;
}

.hs-button {
  width: auto;
}

.btn-invert {
  border: 2px solid #fff0a1;
  background: #fff0a1;
  color: #003240;
}
.btn-invert:hover, .btn-invert:focus {
  background: transparent;
  color: #fff0a1;
  border-color: #fff0a1;
}

.link {
  text-decoration: underline;
  color: #003240;
}

.link-roboto {
  font-family: "Roboto Slab", "Century Gothic", sans-serif;
  text-decoration: underline;
}

.btn-close {
  display: flex;
  align-items: flex-end;
  margin: 3px 0 0;
  font-weight: 900;
  line-height: 1;
  color: #fff0a1;
}
.btn-close:hover, .btn-close:focus {
