}
.btn-close > span {
  text-decoration: underline;
}
.btn-close .icon {
  width: 9px;
  height: 9px;
  margin: 0 0 2px 5px;
}

.btn-filter {
  padding: 5px 10px 5px 0;
  position: relative;
}
.btn-filter:hover, .btn-filter:focus, .btn-filter.on {
  color: #3aafa9;
}
.btn-filter.on .icon {
  opacity: 1;
}
.btn-filter .icon {
  width: 8px;
  height: 8px;
  position: absolute;
  top: 0;
  right: 0;
  pointer-events: none;
  color: #3aafa9;
  opacity: 0;
}

.share {
  display: flex;
