    border-style: solid;
    border-color: transparent;
  }
}
.slider {
  height: 70vh;
  max-height: 540px;
  min-height: 300px;
  position: relative;
  margin: 0 0 85px;
}
.slider .container {
  height: 100%;
}

.slider-title {
  display: inline-flex;
  height: calc(100% - 100px);
  padding-bottom: 50px;
  position: relative;
  z-index: 10;
  align-items: flex-end;
}

.slider-nav {
  display: flex;
  margin: 20px 0 0;
  list-style: none;
  color: #fff;
}
.slider-nav > li {
  height: 6px;
  margin: 0 15px 0 0;
}
.slider-nav button {
  width: 6px;
  height: 6px;
  border: 1px solid transparent;
  overflow: hidden;
  border-radius: 50%;
  background: #fff;
}
.slider-nav button.on {
  transform: scale(2);
  border-color: #fff0a1;
  background: transparent;
  pointer-events: none;
}
.slider-nav button:hover, .slider-nav button:focus {
  transform: scale(1.5);
  background: #fff0a1;
}

.slide {
  padding: 0 0 50px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background-size: cover;
  background-position: 50% 0;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s ease-in-out;
}
.slide.on {
  opacity: 1;
  pointer-events: auto;
}
.slide:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background: linear-gradient(rgba(0, 50, 64, 0) 0%, #003240 100%);
}

.slide-content {
  display: flex;
  height: 100%;
  align-items: flex-end;
  justify-content: flex-end;
  line-height: 1.2;
}
.slide-content svg {
  min-width: 100px;
  min-height: 30px;
  max-width: 120px;
  max-height: 50px;
}
.slide-content path,
.slide-content polygon,
.slide-content rect,
.slide-content ellipse,
.slide-content circle,
.slide-content line,
.slide-content polyline {
  fill: #fff;
}
.slide-content a {
  display: flex;
  max-width: 500px;
  align-items: center;
  font-weight: 400;
  color: #fff;
}

.baseline {
  max-width: 340px;
  padding: 0 0 0 20px;
  margin: 0 0 0 20px;
  border-left: 1px solid #fff;
  font-size: 1.4rem;
}

@media (min-width: 768px) {
  .baseline {
    font-size: 2rem;
  }
}
@media (min-width: 960px) {
  .slider-title {
    height: 100%;
  }
  .slider-nav {
    margin: 100px 0 10px;
  }
}
@media (min-width: 1264px) {
  .slider-nav {
    margin: 30px 0 0;
  }
}
/* Home specific style */
.main-title-home {
  font-size: 2rem;
  line-height: 1.2;
  color: #fff;
}
.main-title-home > strong {
  font-size: 3rem;
}

.philo {
  padding: 0 0 85px;
  text-align: center;
}
.philo .title-maj {
  margin-top: 0;
}
.philo strong {
  color: #3aafa9;
}

.philo-list {
  margin: 80px 0 0;
  list-style: none;
  text-transform: uppercase;
}
.philo-list li {
  margin: 0 0 50px;
}
.philo-list li:nth-child(2) a:before {
  background-image: url(../../img/manifesto.svg);
}
.philo-list li:nth-child(3) a:before {
  background-image: url(../../img/history.svg);
}
.philo-list a {
  display: block;
  color: inherit;
}
.philo-list a:before {
  content: "";
  display: block;
  width: 85px;
  height: 85px;
  margin: 0 auto 35px;
  border-radius: 50%;
  background: url(../../img/thesis.svg) no-repeat;
  background-size: 100%;
  box-shadow: 0 1px 26px 0 rgba(0, 50, 64, 0.08);
  transition: 0.3s ease-in-out;
}
.philo-list a:hover, .philo-list a:focus {
  opacity: 0.9;
}
.philo-list a:hover:before, .philo-list a:focus:before {
  transform: scale(0.95);
}

.team-wrapper {
  padding: 75px 0 95px;
}

.team {
  margin: 75px 0 0;
  text-align: center;
}

.videos-wrapper,
.home-news {
  padding: 75px 0 95px;
  background: #f6f7f8;
}

.videos-slider {
  position: relative;
  margin: 65px 0 0;
}

.videos {
  display: flex;
  position: relative;
  opacity: 0;
}

.video-prev,
.video-next {
  width: 25px;
  height: 30px;
  padding: 5px;
  position: absolute;
  top: 0;
  margin: 25% 0 0;
  line-height: 1;
  color: #fff;
}
.video-prev:hover, .video-prev:focus,
.video-next:hover,
.video-next:focus {
  opacity: 0.7;
}
.video-prev > .icon,
.video-next > .icon {
  height: 100%;
}

.video-prev {
  left: 16px;
  text-align: right;
}

.video-next {
  right: 16px;
}

.video-next-big,
.video-prev-big {
  display: none;
}

.slide-video {
  width: 100%;
  flex-shrink: 0;
  pointer-events: none;
}
.slide-video .video-wrapper {
  overflow: hidden;
  transform: translate3d(0, 0, 0) scale(0.9);
  transform-origin: 50% 50%;
  transition: 0.3s ease-in-out;
  will-change: transform;
}
.slide-video .video {
  filter: blur(11px);
  transition: 0.3s ease-in-out;
}
.slide-video .cover:before, .slide-video .cover:after {
  opacity: 0;
}
.slide-video .video-text,
.slide-video .btn {
  display: none;
}
.slide-video.on {
  pointer-events: auto;
}
.slide-video.on .video-wrapper {
  transform: translate3d(0, 0, 0) scale(1);
}
.slide-video.on .video {
  filter: blur(0px);
}
.slide-video.on .cover:before, .slide-video.on .cover:after {
  opacity: 1;
}

.video-author {
  display: flex;
  margin-bottom: 2.4em;
  justify-content: center;
  align-items: center;
  text-align: left;
}
.video-author > img {
  display: block;
  width: auto;
  max-width: 80px;
  max-height: 40px;
  margin: 0 10px;
}
.video-author > strong {
  padding: 0 10px;
  font-family: "Roboto Slab", "Century Gothic", sans-serif;
  font-size: 2rem;
  line-height: 1;
  color: #003240;
}

.video-nav {
  display: flex;
  justify-content: space-between;
  margin: 5px -5px 0;
}
.video-nav > li {
  padding: 5px;
  margin: 0;
  flex: 1;
  list-style: none;
}
.video-nav button {
  width: 100%;
  filter: grayscale(80%);
}
.video-nav button:hover, .video-nav button:focus, .video-nav button.on {
  filter: grayscale(30%);
}
.video-nav .cover {
  width: 100%;
  padding: 0 0 56%;
  position: relative;
}
.video-nav .cover:before {
  width: 40px;
  height: 40px;
}
.video-nav .cover:after {
  width: 30px;
  height: 30px;
  background-size: 10px;
}

.video-text-wrapper {
  padding: 0 32px;
  margin: 0 auto;
  text-align: center;
  will-change: contents;
}

.home-contact {
  background: #fff;
}

@media (min-width: 580px) {
  .main-title-home {
    font-size: 3.8rem;
  }
  .main-title-home > strong {
    font-size: 5.3rem;
  }
  .philo-list {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  .philo-list li {
    width: 33%;
  }
}
@media (min-width: 768px) {
  .slide-video {
    width: 75%;
  }
  .video-text-wrapper {
    width: 75%;
    padding: 0;
  }
  .video-prev,
  .video-next {
    margin-top: 19%;
  }
  .video-prev {
    left: 15%;
  }
  .video-next {
    right: 15%;
  }
  .video-next-big,
  .video-prev-big {
    display: block;
    width: 12.5%;
    max-height: 400px;
    position: absolute;
    top: 0;
    bottom: 0;
    overflow: hidden;
    text-indent: -99em;
  }
  .video-next-big {
    right: 0;
  }
  .video-prev-big {
    left: 0;
  }
  .video-nav {
    margin-top: 1.5em;
    justify-content: center;
  }
  .video-nav > li {
    flex: initial;
  }
  .video-nav button {
    width: 12px;
    height: 12px;
    border: 2px solid #fff;
    border-radius: 50px;
    background: #003240;
  }
  .video-nav button:hover, .video-nav button:focus {
    background: #3aafa9;
  }
  .video-nav button.on {
    border-color: #3aafa9;
    background: #fff;
  }
  .video-nav .cover {
    display: none;
  }
}
@media (min-width: 960px) {
  .philo-content {
    width: 66.6666666667%;
    margin: 0 auto;
  }
  .video-text-wrapper {
    text-align: left;
  }
  .video-text {
    display: flex;
    justify-content: space-between;
  }
  .video-author {
    display: block;
    min-width: 170px;
    max-width: 30%;
    margin-left: 75px;
  }
  .video-author > img {
    margin: 10px 0 0;
  }
  .video-author > strong {
    font-size: 2.3rem;
    padding: 0;
  }
}
@media (min-width: 1264px) {
  .philo-content {
    width: 50%;
  }
  .team {
    display: flex;
    align-items: center;
    text-align: left;
  }
  .team > div {
    width: 50%;
  }
  .team .txt {
    padding: 0 0 0 8.3333333333%;
  }
  .team .img > div {
    float: right;
  }
  .team img {
    max-width: 50vw;
  }
  .slide-video,
  .video-text-wrapper {
    width: 50%;
  }
  .video-text-wrapper {
    width: 50%;
  }
  .video-prev,
  .video-next {
    margin-top: 13%;
  }
  .video-prev {
    left: 27%;
  }
  .video-next {
    right: 27%;
  }
  .video-next-big,
  .video-prev-big {
    width: 25%;
  }
}
/******* PRINT *******/
@media print {
  *, *:before {
    color: #000 !important;
  }
  a {
    text-decoration: underline;
  }
  a[href^="#"]:after {
    content: "";
  }
  img {
    page-break-inside: avoid;
  }
  @page {
    margin: 0.5cm;
  }
  p, h2, h3 {
    orphans: 3;
    widows: 3;
  }
  h2, h3 {
    page-break-after: avoid;
  }
}